.c-grid-wrapper {
  overflow: hidden;
  position: relative;
  color: #333;
}
.c-grid-header {
  position: relative;
  background-color: #eaeaea;
  border-top: 1px solid #c6c6c6;
  border-left: 1px solid #c6c6c6;
  border-right: 1px solid #c6c6c6;
  box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.35);
  outline: 0;
  font-weight: bold;
  overflow: hidden;
}
.c-grid-header ul.c-header-row {
  position: relative;
  /*left: -1000px;*/
  display: block;
  list-style: none;
  margin: 0;
  padding: 0;
  white-space: nowrap;
}
.c-grid-header ul.c-header-row > li.c-header-cell {
  /*position: relative;*/
  float: left;
  /*left: 1000px;*/
  background-color: #eaeaea;
  margin: 0;
  box-sizing: border-box;
  line-height: 21px;
  padding: 8px 20px 8px 10px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  border-right: 1px solid #c6c6c6;
  line-height: 22px;
}
.c-grid-header ul.c-header-row > li.c-header-cell:hover {
  background-color: #f1f1f1;
}
.c-header-row .c-col-resizable {
  cursor: col-resize;
}
.c-header-row .c-col-draggable {
  cursor: move;
}
.c-col-placeholder {
  width: 0;
  height: 0;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 9999;
  display: none;
  border: 8px solid #0000;
}
.c-top {
  border-top: 12px solid #32792d;
}
.c-bottom {
  border-bottom: 12px solid #32792d;
}
.c-grid-body {
  background-color: #fff;
  border-style: solid;
  border-color: #c6c6c6;
  border-width: 1px;
}
.c-grid-body .c-grid-viewport {
  width: 100%;
  position: relative;
  overflow: auto;
}
.c-grid-body .c-grid-viewport .c-grid-canvas {
  position: relative;
  -moz-user-select: none;
  -ms-user-select: none;
  -webkit-user-select: none;
  user-select: none;
}
li.c-column-locked {
  background-color: #f9f9f9;
}
/* Menu */
.c-menu {
  position: absolute;
  width: 200px;
  padding: 2px;
  margin: 0;
  border: 1px solid #bbb;
  background: #eee;
  background: -webkit-linear-gradient(to bottom, #fff 0%, #e5e5e5 100px, #e5e5e5 100%);
  background: linear-gradient(to bottom, #fff 0%, #e5e5e5 100px, #e5e5e5 100%);
  z-index: 100;
  border-radius: 3px;
  box-shadow: 1px 1px 4px rgba(0, 0, 0, 0.2);
  opacity: 0;
  -webkit-transform: translate(0, 15px) scale(0.95);
  transform: translate(0, 15px) scale(0.95);
  transition: transform 0.1s ease-out, opacity 0.1s ease-out;
  pointer-events: none;
}
.c-menu-item {
  display: block;
  position: relative;
  margin: 0;
  padding: 0;
  white-space: nowrap;
}
.c-menu-btn {
  background: none;
  line-height: normal;
  overflow: visible;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  display: block;
  width: 100%;
  color: #444;
  font-family: 'Roboto', sans-serif;
  font-size: 13px;
  text-align: left;
  cursor: pointer;
  border: 1px solid transparent;
  white-space: nowrap;
  padding: 6px 8px;
  border-radius: 3px;
}
.c-menu-btn::-moz-focus-inner,
.c-menu-btn::-moz-focus-inner {
  border: 0;
  padding: 0;
}
.c-menu-text {
  margin-left: 25px;
}
.c-menu-btn .fa {
  position: absolute;
  left: 8px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
.c-menu-item:hover > .c-menu-btn {
  color: #fff;
  outline: none;
  background-color: #2E3940;
  background: -webkit-linear-gradient(to bottom, #5D6D79, #2E3940);
  background: linear-gradient(to bottom, #5D6D79, #2E3940);
  border: 1px solid #2E3940;
}
.c-menu-item.disabled {
  opacity: .5;
  pointer-events: none;
}
.c-menu-item.disabled .c-menu-btn {
  cursor: default;
}
.c-menu-separator {
  display: block;
  margin: 7px 5px;
  height: 1px;
  border-bottom: 1px solid #fff;
  background-color: #aaa;
}
.c-menu-item.submenu::after {
  content: "";
  position: absolute;
  right: 6px;
  top: 50%;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  border: 5px solid transparent;
  border-left-color: #808080;
}
.c-menu-item.submenu:hover::after {
  border-left-color: #fff;
}
.c-menu .c-menu {
  top: 4px;
  left: 99%;
}
.show-menu,
.c-menu-item:hover > .c-menu {
  opacity: 1;
  -webkit-transform: translate(0, 0) scale(1);
  transform: translate(0, 0) scale(1);
  pointer-events: auto;
}
.c-menu-item:hover > .c-menu {
  -webkit-transition-delay: 100ms;
  transition-delay: 300ms;
}
